/* roboto-condensed-400 - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-v18-latin_cyrillic-400.woff2") format("woff2"), url("../fonts/roboto-condensed-v18-latin_cyrillic-400.woff") format("woff");
  font-display: swap; }

/* roboto-condensed-700 - latin_cyrillic */
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-v18-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v18-latin_cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  font-display: swap; }

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/bebasneue-700.woff2") format("woff2"), url("../fonts/bebasneue-700.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/bebasneue-400.woff2") format("woff2"), url("../fonts/bebasneue-400.woff") format("woff");
  font-display: swap; }

.button {
  width: 230px;
  font: 700 1rem/1 "Roboto Condensed", sans-serif;
  display: block;
  padding: 1.5rem;
  color: #fff;
  background: 0 0;
  border: 4px solid #fff;
  appearance: none;
  cursor: pointer;
  transition: 0.25s;
  outline: 0;
  text-decoration: none;
  text-align: center; }
  .button:hover {
    opacity: 0.25;
    transition: 0.25s; }

.container {
  margin: 0 6.438rem; }
  @media (max-width: 768px) {
    .container {
      margin: 0 2.813rem; } }
  @media (max-width: 320px) {
    .container {
      margin: 0 0.938rem; } }

.call {
  color: #fff;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 1.625rem;
  letter-spacing: 0.03rem;
  line-height: 1.625rem; }
  @media (max-width: 768px) {
    .call {
      font-size: 1.5rem;
      letter-spacing: 0.02rem;
      line-height: 1.5rem; } }
  @media (max-width: 320px) {
    .call {
      font-size: 1.25rem;
      letter-spacing: 0.02rem;
      line-height: 1.25rem; } }

.logo a {
  transition: opacity 0.25s;
  cursor: pointer; }
  .logo a:hover {
    opacity: 0.25;
    transition: opacity 0.25s; }

.call a {
  color: inherit;
  text-decoration: none;
  transition: opacity 0.25s;
  cursor: pointer; }
  .call a:hover {
    opacity: 0.25;
    transition: opacity 0.25s; }

.main-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%; }

.main-header__container {
  display: flex;
  justify-content: space-between;
  padding: 1.875rem 0 0 0; }

.about {
  width: 100%;
  padding: 3.188rem 0;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg); }

.about__container {
  position: relative;
  display: flex;
  justify-content: flex-end; }

.about__header {
  margin: 0;
  color: #fff;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 6.813rem;
  letter-spacing: 0.03rem;
  line-height: 100%;
  text-align: right; }

.about__text {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 7.5rem 4rem 0 0; }

.about__location {
  order: -1;
  margin: 0 0 0.725rem 0;
  color: #fff;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 400;
  font-size: 3.0625rem;
  letter-spacing: 0.03rem;
  line-height: 3.0625rem;
  text-align: right; }

.about__features-list {
  position: absolute;
  left: -8rem;
  top: 10.5rem;
  list-style: none;
  color: #fff;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  letter-spacing: 0;
  text-align: right;
  line-height: 150%; }

.about__socials-list {
  position: absolute;
  right: 0;
  bottom: 2.8125rem;
  list-style: none;
  color: #fff; }
  @media (max-width: 768px) {
    .about__socials-list {
      display: none; } }

.about__socials-item {
  margin: 0 0 1.5rem 0; }

.about__socials-item a {
  transition: opacity 0.25s; }
  .about__socials-item a:hover {
    opacity: 0.25;
    transition: opacity 0.25s; }

.about__image {
  margin: 0 12.5rem 0 0; }
  @media (max-width: 320px) {
    .about__image {
      display: none; } }
